@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.App {
  text-align: center;
}

.NavbarItems{
  background: transparent;
  transition: background-color 0.3s ease-out;
  scroll-behavior: smooth;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    /* box-shadow: 0 5px 15px 0 rgba(0,0,0,0.25); */
    width: 100%;
    height: 80px;
    position:fixed;
    z-index: 9999;
    

}
.navbar-logo a{
  text-decoration: none;
  color:#AC2900;
}

.navbar-logo a:hover{
  transition: 0.2s ease-in;
  color:white;
}


.navbar-logo{
    color:#AC2900;
    font-size: 1.5rem;
    cursor: pointer;
    text-decoration: none !important;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
}
.linksdiv{
  padding: 4px 0;
  border-bottom: 3px solid transparent;
}
.linksdiv:hover{
  border-bottom: 3px solid #AC2900;
  transition: 0.3s ease-in;
}
.nav-links{
    text-decoration: none;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    padding: 0.7rem 1rem;
    white-space: nowrap;

}

.nav-links i{
  padding-right: 10px;

}

.nav-links:hover{
    
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
  
  }

  .nav-links-mobile{
    display: none;
  
  }

  .signupbtn{
    padding:0.5rem 1rem ;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;

  }

  .signupbtn:hover{
    background-color: #151516;
    color: #fff;
  }
.menu-icons{
    display: none;
}

  @media screen and (max-width:850px){
    .NavbarItems{
        z-index: 99;
    }

    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color: #fff;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        align-items:stretch;
        padding: 80px 0 30px 0 ;
        margin: 0;
        transition: 0.3s ease-in-out;
        
    }

    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;
    }

    .nav-links{
      color: #1f1f1f;
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }

    
    .nav-links:hover{
        background: #01959a;
        transition: none;
    }
    .signupbtn{
        display: none;
    
      }
    
  .nav-links-mobile{
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: auto;
    border-radius: 4px;
    width: 80%;
    background:#AC2900;
    text-decoration: none;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: 2px;
  }

  .menu-icons{
    display: block;
    cursor: pointer;
}
.menu-icons i{
   font-size: 1.2rem;
   color: #222;
}
      
  }
.destination{
    margin: 4rem 6rem;

}

.destination h1{
    font-size: 3rem;

}

.first-des{
    display: flex;
    align-items: center ;
    justify-content: space-between;
    margin-top: 4.5rem;
}

.first-des-reverse{
    display: flex;
    flex-direction: row-reverse;
    align-items: center ;
    justify-content: space-between;
    margin-top: 4.5rem;
}

.des-text{
    width: 45%;
    text-align: start;
    font-size: 1.1rem;

}


.des-text h2{
   padding-bottom: 1rem;
    
}

.image{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;
}

.image img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -18px rgba(0,0,0,0.19);

}

.image img:nth-child(2){
    position: absolute;
    top: -10%;
    right: 0;
}

@media screen and (max-width:850px){
    .destination{
        margin: 4rem 2rem;
    
    }
    .first-des{
        display: flex;
        flex-direction: column-reverse;
        margin-top: 2.5rem;
    }
    .first-des-reverse{
        display: flex;
        flex-direction: column-reverse;
       
        margin-top: 4.5rem;
    }
    .des-text{
        width: 100%;
    }

    .image{
        width: 100%;
        margin: 1.8rem 0;
    }

    .image img{
        height: 250px;
    }
}
.footer{
    padding: 4rem 6rem;
    background:#1f1f1f;
    color: #fff;

}

.top{
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
}

.top i{
    color: #fff;
    font-size: 2rem;
    margin-left: 1rem;
}

.top i:hover{
    color: #01959a;
}

.bottom{
    padding-top: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap ;

}

.bottom div{
    display: flex;
    flex-direction: column;
}

.bottom h4{
    font-size: 1.3rem;
    padding: 1rem 0 0.8rem 0;
}

.bottom a{
    text-decoration: none;
    color:#fafafa;
    padding-bottom: 0.4rem;
    font-size: 1.1rem;
}

@media screen and (max-width:850px){
    .footer{
        padding: 4rem 2rem;
    }

    .footer i{
        margin: 1rem 1rem 0 0 ;
    }

    .bottom div{
        width: 50%;
    }
}
.hero{
   width:100%;
   height: 70vh;
   position: relative; 
}

.hero-mid{
    width:100%;
    height: 70vh;
    position: relative; 
 }

 .hero-mid h1{
    padding-top: 4rem !important; 
 }
img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.hero-text{
    position: absolute;
    top:55%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
}

.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}
.hero-text p{
    font-size: 1.3rem;
    color: #fff;
    padding: 0.5rem 0 2rem 0;

}

.hero-text .show{
    display: inline-block;
    padding: 12px 18px;
    overflow: hidden;
    color: rgb(255, 255, 255);
    text-decoration: none;
   
    border-radius: 3px;
    background-color:#AC2900;
  border: 1px solid #AC2900;

}

.hero-text .show:hover{
    background-position: 100% 50%;
    transition: .3s ease-in;
    background-color: #AC2900;
  border-color:#AC2900;
  color: white;
 
 }

.hero-text .hide{
    display: none;
 
 }

 
  

@media screen and (max-width:555px){
.hero-text h1{
    padding: 10px 20px;
}
.hero-text p {
    font-size: 1.1rem;
    padding: 0 0 2rem 0 ;
}

.hero-text .show{
    padding: 0.6rem 1.1rem;
    border-radius: 6px;
    font-size: 1rem;
 }
}
.trip{
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.trip h1{
    font-size: 3rem;
}

.tripcard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}

.t-card{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0,0,0,0.11);
    border-radius: 7px;
    cursor: pointer;
    

}

.t-card a{
  text-decoration: none;
  color: #2a2a2a;

}

.t-image{
    height: 250px;
    overflow: hidden;
    /* border-radius: 7px  7px 0 0 ; */

}


.t-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px  7px 0 0 ;
   
}


.t-image:hover img{
   -webkit-transform: scale(1.3);
           transform: scale(1.3);
   border-radius: 7px  7px 0 0 ;
}
.textdiv{
        padding: 0.2rem 1rem 0.8rem 1rem;
}

.statename{
    color:#AC2900;
    font-weight: 500;
}
.t-card h4{
    font-size: 1.3rem;
    padding: 0 0 .2rem 0;
}

@media screen and (max-width:850px){
    .trip{
        margin: 4rem 2rem;
    }

    .tripcard{
   
        flex-direction: column;
        
    }

    .t-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}
