.trip{
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.trip h1{
    font-size: 3rem;
}

.tripcard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}

.t-card{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0,0,0,0.11);
    border-radius: 7px;
    cursor: pointer;
    

}

.t-card a{
  text-decoration: none;
  color: #2a2a2a;

}

.t-image{
    height: 250px;
    overflow: hidden;
    /* border-radius: 7px  7px 0 0 ; */

}


.t-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px  7px 0 0 ;
   
}


.t-image:hover img{
   transform: scale(1.3);
   border-radius: 7px  7px 0 0 ;
}
.textdiv{
        padding: 0.2rem 1rem 0.8rem 1rem;
}

.statename{
    color:#AC2900;
    font-weight: 500;
}
.t-card h4{
    font-size: 1.3rem;
    padding: 0 0 .2rem 0;
}

@media screen and (max-width:850px){
    .trip{
        margin: 4rem 2rem;
    }

    .tripcard{
   
        flex-direction: column;
        
    }

    .t-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}